"use client";

import { cn } from "@/utils/helper";
import { ButtonHTMLAttributes, ReactNode } from "react";

interface CommonButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  variant?: "primary" | "secondary" | "outline" | "danger";
  icon?: ReactNode;
  children: ReactNode;
}

export default function CommonButton({
  variant = "primary",
  icon,
  children,
  className,
  ...props
}: CommonButtonProps) {
  const baseStyles =
    "flex items-center justify-center gap-2 px-4 py-2 rounded-md font-medium transition-all cursor-pointer";

  const variants: Record<string, string> = {
    primary: "bg-(--color-primary) text-(--color-secondary) hover:opacity-60",
    secondary:
      "bg-transparent text-[--color-primary] border border-(--color-primary) hover:bg-(--color-primary) hover:text-(--color-secondary)",
    outline:
      "border border-(--color-primary) text-(--color-primary) hover:bg-(--color-primary) hover:text-(--color-secondary)",
    danger: "bg-red-600 text-white hover:bg-red-700",
  };

  return (
    <button className={cn(baseStyles, variants[variant], className)} {...props}>
      {icon && icon}
      {children}
    </button>
  );
}
